* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  color: #dcddde;
  font-smoothing: antialiased;
  font-size: 1.6rem;
  background: #2c3338;
}

img, embed, object, video {
  max-width: 100%;
}

li {
  list-style-position: inside;
}

@media only screen and (max-width: 767px) {
  .navbar {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 0;
    left: -1000px;
    justify-content: center;
    align-content: center;
    align-items: center;
    flex-wrap: nowrap;
    background: #2c3338;
    border: 0 solid transparent;
    letter-spacing: 1px;
    z-index: 101;
    padding-top: 5rem;
    height: 100vh;
    width: 100vw;
    transition: all 0.3s linear;
  }
  .navbar.open {
    left: 0;
    z-index: 200;
  }
  .navbar > .navbar-menu {
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
    flex-wrap: nowrap;
    width: 100vw;
    height: 100vh;
  }
  .navbar > .navbar-menu:last-child > .navlabel:first-child {
    border-top: 1px solid transparent;
  }
  .navbar > .navbar-menu > .navlabel {
    display: inline-flex;
    background: #2c3338;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 33.33%;
    text-align: center;
    min-height: 2rem;
    border-bottom: 1px solid #e4cd8a;
    z-index: 200;
  }
  .navbar > .navbar-menu > .navlabel:first-child {
    border-top: 1px solid #e4cd8a;
  }
  .navbar > .navbar-menu > .navlabel:hover {
    background: #424d55;
  }
  .navbar > .navbar-menu > .navlabel > .navtext {
    font-family: "Sansita", sans-serif;
    font-size: 2rem;
    letter-spacing: 1px;
    color: #e4cd8a;
    user-select: none;
    z-index: 200;
  }
  .navbar > .navbar-menu > .navlabel > .navicon {
    display: none;
  }
  .hamburger {
    position: fixed;
    top: 0;
    left: 0;
    width: 5rem;
    height: 5rem;
    padding: 1rem;
    background: #2c3338;
    cursor: pointer;
    margin: 0 auto;
    z-index: 2300;
  }
  .hamburger::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 5rem;
    background-color: #2c3338;
    z-index: -100;
  }
  .hamburger > .hamburger-panel {
    background-color: #e4cd8a;
    width: 3rem;
    height: .6rem;
    margin-bottom: 0.6rem;
    transition: all 0.3s linear;
  }
  .hamburger.open .hamburger-1 {
    transform: translateY(1.2rem) rotate(45deg);
  }
  .hamburger.open .hamburger-2 {
    opacity: 0;
  }
  .hamburger.open .hamburger-3 {
    transform: translateY(-1.2rem) rotate(-45deg);
  }
  .client-pagination-wrapper {
    margin: 3rem auto;
  }
  .client-pagination {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  .client-number {
    font-family: "Playfair Display", serif;
    font-size: 3rem;
    font-weight: 600;
    line-height: 3rem;
    margin: 1.5rem;
    color: #e4cd8a;
    background: #2c3338;
    padding: 2rem;
    border-radius: 4px;
  }
  .client-number:hover {
    color: #e4ddce;
    background: #597c80;
    padding: 2rem;
    border-radius: 4px;
    transition: all 0.3s linear;
  }
  .client-number.active {
    color: #e4cd8a;
    background: #597c80;
  }
  .client-number.active:hover {
    color: #e4cd8a;
    background: #597c80;
  }
}

@media only screen and (orientation: portrait) and (max-width: 767px) {
  .client-pagination-wrapper {
    margin: 0 auto 10rem auto;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1279px) {
  .hamburger {
    display: none;
  }
  .navbar {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
    flex-direction: column;
    position: fixed;
    top: 0;
    background: #2c3338;
    border: 0 solid transparent;
    letter-spacing: 1px;
    z-index: 101;
    margin: 0;
    min-height: 10rem;
    width: 100%;
    border-bottom: 1px solid #e4cd8a;
    transition: all 0.3s linear;
  }
  .navbar > .navbar-menu {
    display: flex;
    justify-content: center;
    margin: 0;
  }
  .navbar > .navbar-menu > .navlabel {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 30px;
    margin: auto;
    transition: all 0.3s linear;
    height: 50px;
  }
  .navbar > .navbar-menu > .navlabel:hover {
    background: #424d55;
  }
  .navbar > .navbar-menu > .navlabel > .navtext {
    font-family: "Sansita", sans-serif;
    font-size: 1.6rem;
    letter-spacing: 1px;
    color: #e4cd8a;
    user-select: none;
    transition: all 0.3s linear;
    line-height: 100%;
    margin: 0 0 0 .7rem;
  }
  .navbar > .navbar-menu > .navlabel > .navicon {
    fill: #8eadB1;
  }
  .client-pagination-wrapper {
    margin: 0 auto 10rem auto;
  }
  .client-pagination {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
